"use client";

import { useState } from "react";
import { useRouter } from "next/navigation";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Alert, AlertDescription } from "@/components/ui/alert";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import DataInput from "@/components/DroughtMonitor/DataInput";
import MapInput from "@/components/DroughtMonitor/MapInput";
import UploadKML from "@/components/DroughtMonitor/UploadKML";

interface LocationData {
  lat: number;
  lng: number;
  name?: string;
}

export default function HomePage() {
  const router = useRouter();
  const [selectedLocation, setSelectedLocation] = useState<LocationData | null>(null);

  const handleLocationSelect = (location: LocationData) => {
    setSelectedLocation(location);
  };

  const handleStudyLocation = () => {
    if (selectedLocation) {
      const params = new URLSearchParams({
        lat: selectedLocation.lat.toString(),
        lng: selectedLocation.lng.toString(),
        name: selectedLocation.name || ''
      });
      router.push(`/drought-monitor?${params.toString()}`);
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 to-green-50">
      {/* Header */}
      <div className="bg-white shadow-sm border-b">
        <div className="max-w-7xl mx-auto px-4 py-6">
          <div className="text-center">
            <h1 className="text-4xl font-bold text-gray-900 mb-2">
              نظام مراقبة الجفاف المتقدم
            </h1>
            <p className="text-xl text-gray-600">
              اختر الموقع المراد دراسته لتحليل مؤشرات الجفاف والبيانات المناخية
            </p>
          </div>
        </div>
      </div>

      <div className="max-w-7xl mx-auto p-6 space-y-6">
        {/* خيارات تحديد الموقع */}
        <Card>
          <CardHeader>
            <CardTitle className="text-right">طرق تحديد الموقع</CardTitle>
            <CardDescription className="text-right">
              اختر الموقع باستخدام إحدى الطرق التالية
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Tabs defaultValue="search" className="w-full">
              <TabsList className="grid w-full grid-cols-4">
                <TabsTrigger value="search">البحث بالاسم</TabsTrigger>
                <TabsTrigger value="coordinates">الإحداثيات</TabsTrigger>
                <TabsTrigger value="map">النقر على الخريطة</TabsTrigger>
                <TabsTrigger value="upload">رفع ملف KML</TabsTrigger>
              </TabsList>

              <TabsContent value="search">
                <DataInput onLocationSelect={handleLocationSelect} />
              </TabsContent>

              <TabsContent value="coordinates">
                <DataInput onLocationSelect={handleLocationSelect} coordinatesMode />
              </TabsContent>

              <TabsContent value="map">
                <MapInput onLocationSelect={handleLocationSelect} />
              </TabsContent>

              <TabsContent value="upload">
                <UploadKML onLocationSelect={handleLocationSelect} />
              </TabsContent>
            </Tabs>
          </CardContent>
        </Card>

        {/* الخريطة التفاعلية */}
        <Card>
          <CardContent className="p-0 h-[500px]">
            <MapInput onLocationSelect={handleLocationSelect} />
          </CardContent>
        </Card>

        {/* زر دراسة الموقع */}
        <div className="text-center">
          <Button
            onClick={handleStudyLocation}
            disabled={!selectedLocation}
            size="lg"
            className="px-12 py-4 text-xl"
          >
            {selectedLocation ? "دراسة الموقع" : "يرجى تحديد موقع أولاً"}
          </Button>
        </div>
      </div>
    </div>
  );
}
