"use client";

import { useState, useRef } from "react";
import { Card, CardContent } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Alert, AlertDescription } from "@/components/ui/alert";

interface LocationData {
  lat: number;
  lng: number;
  name?: string;
  polygon?: Array<[number, number]>;
}

interface UploadKMLProps {
  onLocationSelect: (location: LocationData) => void;
}

export default function UploadKML({ onLocationSelect }: UploadKMLProps) {
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [fileContent, setFileContent] = useState<string | null>(null);
  const fileInputRef = useRef<HTMLInputElement>(null);

  const handleFileSelect = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (!file) return;

    // التحقق من نوع الملف
    const allowedTypes = ['.kml', '.kmz'];
    const fileExtension = file.name.toLowerCase().substring(file.name.lastIndexOf('.'));
    
    if (!allowedTypes.includes(fileExtension)) {
      setError("يرجى اختيار ملف KML أو KMZ فقط");
      return;
    }

    // التحقق من حجم الملف (5MB كحد أقصى)
    if (file.size > 5 * 1024 * 1024) {
      setError("حجم الملف يجب أن يكون أقل من 5 ميجابايت");
      return;
    }

    setSelectedFile(file);
    setError(null);
  };

  const parseKMLContent = (content: string): LocationData | null => {
    try {
      // البحث عن إحداثيات في محتوى KML
      const coordinatesMatch = content.match(/<coordinates>([\s\S]*?)<\/coordinates>/);
      if (!coordinatesMatch) {
        throw new Error("لم يتم العثور على إحداثيات في الملف");
      }

      const coordinatesText = coordinatesMatch[1].trim();
      const coordinates = coordinatesText.split(/\s+/).map(coord => {
        const parts = coord.split(',');
        return [parseFloat(parts[1]), parseFloat(parts[0])]; // lat, lng
      }).filter(coord => !isNaN(coord[0]) && !isNaN(coord[1]));

      if (coordinates.length === 0) {
        throw new Error("لم يتم العثور على إحداثيات صحيحة");
      }

      // البحث عن اسم المكان
      const nameMatch = content.match(/<name>(.*?)<\/name>/);
      const placeName = nameMatch ? nameMatch[1] : "موقع من ملف KML";

      if (coordinates.length === 1) {
        // نقطة واحدة
        return {
          lat: coordinates[0][0],
          lng: coordinates[0][1],
          name: placeName
        };
      } else {
        // مضلع
        const centerLat = coordinates.reduce((sum, coord) => sum + coord[0], 0) / coordinates.length;
        const centerLng = coordinates.reduce((sum, coord) => sum + coord[1], 0) / coordinates.length;
        
        return {
          lat: centerLat,
          lng: centerLng,
          name: placeName,
          polygon: coordinates as Array<[number, number]>
        };
      }
    } catch (err) {
      throw new Error("خطأ في تحليل محتوى الملف");
    }
  };

  const handleFileUpload = async () => {
    if (!selectedFile) return;

    setLoading(true);
    setError(null);

    try {
      const reader = new FileReader();
      
      reader.onload = async (e) => {
        try {
          let content = e.target?.result as string;
          
          if (selectedFile.name.toLowerCase().endsWith('.kmz')) {
            // للملفات المضغوطة KMZ، نحتاج لاستخراج محتوى KML
            // هذا مثال مبسط - في التطبيق الحقيقي نحتاج مكتبة لفك الضغط
            setError("ملفات KMZ غير مدعومة حالياً. يرجى استخدام ملف KML");
            setLoading(false);
            return;
          }

          setFileContent(content);
          const locationData = parseKMLContent(content);
          
          if (locationData) {
            onLocationSelect(locationData);
          }
        } catch (err) {
          setError(err instanceof Error ? err.message : "خطأ في قراءة الملف");
        } finally {
          setLoading(false);
        }
      };

      reader.onerror = () => {
        setError("خطأ في قراءة الملف");
        setLoading(false);
      };

      reader.readAsText(selectedFile);
    } catch (err) {
      setError("خطأ في معالجة الملف");
      setLoading(false);
    }
  };

  const clearFile = () => {
    setSelectedFile(null);
    setFileContent(null);
    setError(null);
    if (fileInputRef.current) {
      fileInputRef.current.value = '';
    }
  };

  return (
    <Card>
      <CardContent className="p-6">
        <div className="space-y-4">
          {/* منطقة رفع الملف */}
          <div className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center hover:border-gray-400 transition-colors">
            <div className="text-4xl mb-4">📁</div>
            <h3 className="text-lg font-semibold mb-2">رفع ملف KML/KMZ</h3>
            <p className="text-gray-600 mb-4">
              اسحب الملف هنا أو انقر للاختيار
            </p>
            
            <input
              ref={fileInputRef}
              type="file"
              accept=".kml,.kmz"
              onChange={handleFileSelect}
              className="hidden"
            />
            
            <Button
              variant="outline"
              onClick={() => fileInputRef.current?.click()}
              disabled={loading}
            >
              اختيار ملف
            </Button>
          </div>

          {/* معلومات الملف المحدد */}
          {selectedFile && (
            <div className="bg-blue-50 p-4 rounded-lg">
              <div className="flex justify-between items-center">
                <div className="text-right">
                  <p className="font-medium text-blue-900">{selectedFile.name}</p>
                  <p className="text-sm text-blue-700">
                    الحجم: {(selectedFile.size / 1024).toFixed(1)} كيلوبايت
                  </p>
                </div>
                <Button
                  variant="outline"
                  size="sm"
                  onClick={clearFile}
                  disabled={loading}
                >
                  إزالة
                </Button>
              </div>
            </div>
          )}

          {/* رسائل الخطأ */}
          {error && (
            <Alert className="border-red-200 bg-red-50">
              <AlertDescription className="text-right text-red-700">
                {error}
              </AlertDescription>
            </Alert>
          )}

          {/* معاينة محتوى الملف */}
          {fileContent && (
            <div className="bg-gray-50 p-4 rounded-lg">
              <h4 className="font-medium mb-2 text-right">معاينة محتوى الملف:</h4>
              <div className="text-sm text-gray-600 max-h-32 overflow-y-auto">
                <pre className="whitespace-pre-wrap text-xs">
                  {fileContent.substring(0, 500)}
                  {fileContent.length > 500 && '...'}
                </pre>
              </div>
            </div>
          )}

          {/* زر المعالجة */}
          {selectedFile && (
            <Button
              onClick={handleFileUpload}
              disabled={loading}
              className="w-full"
            >
              {loading ? (
                <div className="flex items-center justify-center">
                  <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-white mr-2"></div>
                  جاري معالجة الملف...
                </div>
              ) : (
                "معالجة الملف واستخراج الإحداثيات"
              )}
            </Button>
          )}

          {/* تعليمات */}
          <div className="text-sm text-gray-500 text-right space-y-1">
            <p><strong>الملفات المدعومة:</strong> KML (.kml)</p>
            <p><strong>الحد الأقصى للحجم:</strong> 5 ميجابايت</p>
            <p><strong>المحتوى المدعوم:</strong> النقاط والمضلعات</p>
          </div>

          {/* مثال على تنسيق KML */}
          <details className="text-sm">
            <summary className="cursor-pointer text-blue-600 hover:text-blue-800">
              مثال على تنسيق KML المدعوم
            </summary>
            <div className="mt-2 p-3 bg-gray-100 rounded text-xs">
              <pre>{`<?xml version="1.0" encoding="UTF-8"?>
<kml xmlns="http://www.opengis.net/kml/2.2">
  <Document>
    <Placemark>
      <name>موقع تجريبي</name>
      <Point>
        <coordinates>46.6753,24.7136,0</coordinates>
      </Point>
    </Placemark>
  </Document>
</kml>`}</pre>
            </div>
          </details>
        </div>
      </CardContent>
    </Card>
  );
}
